'use client'

import { useState, useEffect } from 'react'
import axios from 'axios'
import { Search, Loader2, ArrowRight, Plus, Trash2, X, AppWindow, Star, Download } from 'lucide-react'
import { motion, AnimatePresence } from 'framer-motion'
import Link from 'next/link'
import { useRouter } from 'next/navigation'

export default function AppSelectionPage() {
    const router = useRouter()
    const [savedApps, setSavedApps] = useState([])
    const [showAddModal, setShowAddModal] = useState(false)
    const [searchTerm, setSearchTerm] = useState('')
    const [searchResults, setSearchResults] = useState([])
    const [isSearching, setIsSearching] = useState(false)
    const [error, setError] = useState(null)

    // Load saved apps from localStorage on mount
    useEffect(() => {
        loadSavedApps()
    }, [])

    const loadSavedApps = () => {
        if (typeof window !== 'undefined') {
            const saved = localStorage.getItem('savedApps')
            if (saved) {
                setSavedApps(JSON.parse(saved))
            }
        }
    }

    const saveApp = (app) => {
        if (typeof window !== 'undefined') {
            const existing = savedApps.find(a => a.appId === app.appId)
            if (!existing) {
                const updated = [...savedApps, app]
                setSavedApps(updated)
                localStorage.setItem('savedApps', JSON.stringify(updated))
            }
            // Navigate to tracker
            router.push(`/tracker?appId=${app.appId}&title=${encodeURIComponent(app.title)}&icon=${encodeURIComponent(app.icon)}&developer=${encodeURIComponent(app.developer)}`)
        }
    }

    const removeApp = (appId, e) => {
        e.preventDefault()
        e.stopPropagation()
        if (typeof window !== 'undefined') {
            const updated = savedApps.filter(a => a.appId !== appId)
            setSavedApps(updated)
            localStorage.setItem('savedApps', JSON.stringify(updated))
        }
    }

    const processSearch = async (e) => {
        e.preventDefault()
        const term = searchTerm.trim()
        if (!term) return

        setError(null)
        setSearchResults([])

        // 1. Direct Link/ID Navigation strategy (Zero API)
        // Check if URL
        if (term.includes('play.google.com')) {
            try {
                const url = new URL(term);
                const id = url.searchParams.get('id');
                if (id) {
                    // Fetch app details and save
                    fetchAndSaveApp(id)
                    return;
                }
            } catch (e) { }
        }

        // Check if likely Package ID (contains dot, no spaces)
        if (term.includes('.') && !term.includes(' ')) {
            fetchAndSaveApp(term)
            return;
        }

        // 2. Search API Strategy (Only if needed)
        setIsSearching(true)
        try {
            const { data } = await axios.get(`/api/apps/search?term=${term}`)
            if (data.length === 0) {
                setError('No apps found')
            } else {
                setSearchResults(data)
            }
        } catch (error) {
            setError('Search failed. Please try again.')
        } finally {
            setIsSearching(false)
        }
    }

    const fetchAndSaveApp = async (appId) => {
        setIsSearching(true)
        try {
            const { data } = await axios.get(`/api/apps/details?appId=${appId}`)
            saveApp({
                appId: data.appId,
                title: data.title,
                icon: data.icon,
                developer: data.developer
            })
            setShowAddModal(false)
            setSearchTerm('')
            setSearchResults([])
        } catch (error) {
            setError('Failed to fetch app details')
        } finally {
            setIsSearching(false)
        }
    }

    return (
        <div className="max-w-6xl mx-auto px-6 py-12 min-h-screen">
            {/* Header */}
            <header className="mb-12">
                <div className="flex items-center justify-between">
                    <div>
                        <h1 className="text-4xl font-bold tracking-tight text-[var(--text-main)] mb-2">
                            Choose an App
                        </h1>
                        <p className="text-[var(--text-muted)] text-lg">
                            Select an app to track or add a new one
                        </p>
                    </div>
                    <button
                        onClick={() => setShowAddModal(true)}
                        className="pro-button flex items-center gap-2 px-6 py-3"
                    >
                        <Plus className="w-5 h-5" />
                        Add App
                    </button>
                </div>
            </header>

            {/* Saved Apps Grid */}
            {savedApps.length === 0 ? (
                <div className="flex flex-col items-center justify-center py-20 text-center">
                    <AppWindow className="w-20 h-20 text-[var(--text-muted)] opacity-30 mb-6" />
                    <h2 className="text-2xl font-bold text-[var(--text-main)] mb-2">No Apps Yet</h2>
                    <p className="text-[var(--text-muted)] mb-6 max-w-md">
                        Get started by adding your first app to track its performance and rankings
                    </p>
                    <button
                        onClick={() => setShowAddModal(true)}
                        className="pro-button flex items-center gap-2 px-6 py-3"
                    >
                        <Plus className="w-5 h-5" />
                        Add Your First App
                    </button>
                </div>
            ) : (
                <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
                    {savedApps.map((app) => (
                        <Link
                            key={app.appId}
                            href={`/tracker?appId=${app.appId}&title=${encodeURIComponent(app.title)}&icon=${encodeURIComponent(app.icon)}&developer=${encodeURIComponent(app.developer)}`}
                        >
                            <motion.div
                                initial={{ opacity: 0, y: 20 }}
                                animate={{ opacity: 1, y: 0 }}
                                className="glass-card p-6 hover:border-blue-500/50 cursor-pointer group relative"
                            >
                                {/* Remove Button */}
                                <button
                                    onClick={(e) => removeApp(app.appId, e)}
                                    className="absolute top-4 right-4 p-2 rounded-lg bg-red-500/10 hover:bg-red-500/20 text-red-500 opacity-0 group-hover:opacity-100 transition-opacity"
                                >
                                    <Trash2 className="w-4 h-4" />
                                </button>

                                {/* App Icon */}
                                <div className="flex items-center gap-4 mb-4">
                                    <img
                                        src={app.icon}
                                        alt={app.title}
                                        className="w-16 h-16 rounded-2xl shadow-lg"
                                    />
                                    <div className="flex-1 min-w-0">
                                        <h3 className="font-bold text-[var(--text-main)] text-lg truncate mb-1">
                                            {app.title}
                                        </h3>
                                        <p className="text-sm text-[var(--text-muted)] truncate">
                                            {app.developer}
                                        </p>
                                    </div>
                                </div>

                                {/* Arrow Indicator */}
                                <div className="flex items-center justify-end mt-4">
                                    <ArrowRight className="w-5 h-5 text-[var(--text-muted)] group-hover:text-blue-500 group-hover:translate-x-1 transition-all" />
                                </div>
                            </motion.div>
                        </Link>
                    ))}
                </div>
            )}

            {/* Add App Modal */}
            <AnimatePresence>
                {showAddModal && (
                    <motion.div
                        initial={{ opacity: 0 }}
                        animate={{ opacity: 1 }}
                        exit={{ opacity: 0 }}
                        className="fixed inset-0 bg-black/60 backdrop-blur-sm z-50 flex items-center justify-center p-6"
                        onClick={() => {
                            setShowAddModal(false)
                            setSearchTerm('')
                            setSearchResults([])
                            setError(null)
                        }}
                    >
                        <motion.div
                            initial={{ scale: 0.95, y: 20 }}
                            animate={{ scale: 1, y: 0 }}
                            exit={{ scale: 0.95, y: 20 }}
                            className="bg-[var(--card-bg)] border border-[var(--card-border)] rounded-2xl p-8 max-w-2xl w-full max-h-[80vh] overflow-y-auto"
                            onClick={(e) => e.stopPropagation()}
                        >
                            {/* Modal Header */}
                            <div className="flex items-center justify-between mb-6">
                                <h2 className="text-2xl font-bold text-[var(--text-main)]">Add New App</h2>
                                <button
                                    onClick={() => {
                                        setShowAddModal(false)
                                        setSearchTerm('')
                                        setSearchResults([])
                                        setError(null)
                                    }}
                                    className="p-2 hover:bg-[var(--input-bg)] rounded-lg transition-colors"
                                >
                                    <X className="w-5 h-5 text-[var(--text-muted)]" />
                                </button>
                            </div>

                            <p className="text-[var(--text-muted)] mb-6">
                                Search for an app by name, or enter a Google Play URL or package ID
                            </p>

                            {/* Search Form */}
                            <form onSubmit={processSearch} className="relative group mb-6">
                                <Search className="absolute left-5 top-1/2 -translate-y-1/2 text-slate-400 w-5 h-5" />
                                <input
                                    type="text"
                                    placeholder="e.g. Instagram, com.instagram.android, or Play Store URL..."
                                    className="pro-input pl-14 pr-24 py-4 text-lg w-full"
                                    value={searchTerm}
                                    onChange={(e) => setSearchTerm(e.target.value)}
                                    autoFocus
                                />
                                <div className="absolute right-2 top-1/2 -translate-y-1/2">
                                    <button
                                        type="submit"
                                        disabled={isSearching || !searchTerm.trim()}
                                        className="pro-button py-2 px-4 text-sm"
                                    >
                                        {isSearching ? <Loader2 className="w-4 h-4 animate-spin" /> : 'Search'}
                                    </button>
                                </div>
                            </form>

                            {/* Error State */}
                            {error && (
                                <motion.div
                                    initial={{ opacity: 0 }}
                                    animate={{ opacity: 1 }}
                                    className="text-center text-red-500 text-sm font-medium mb-4 p-3 bg-red-500/10 rounded-lg"
                                >
                                    {error}
                                </motion.div>
                            )}

                            {/* Search Results */}
                            <AnimatePresence mode="wait">
                                {searchResults.length > 0 && (
                                    <motion.div
                                        key="results"
                                        initial={{ opacity: 0, y: 10 }}
                                        animate={{ opacity: 1, y: 0 }}
                                        className="space-y-3"
                                    >
                                        <div className="text-xs font-semibold uppercase tracking-wider text-[var(--text-muted)] mb-3">
                                            Select an app to add
                                        </div>
                                        {searchResults.map((app) => (
                                            <div
                                                key={app.appId}
                                                onClick={() => saveApp(app)}
                                                className="glass-card p-4 flex items-center gap-4 group cursor-pointer hover:border-blue-500/50"
                                            >
                                                <img src={app.icon} className="w-14 h-14 rounded-xl" alt="" />
                                                <div className="flex-1 min-w-0">
                                                    <h3 className="font-medium text-[var(--text-main)] truncate mb-1">
                                                        {app.title}
                                                    </h3>
                                                    <p className="text-sm text-[var(--text-muted)] truncate">
                                                        {app.developer}
                                                    </p>
                                                </div>
                                                <Plus className="w-5 h-5 text-[var(--text-muted)] group-hover:text-blue-500 transition-colors" />
                                            </div>
                                        ))}
                                    </motion.div>
                                )}
                            </AnimatePresence>
                        </motion.div>
                    </motion.div>
                )}
            </AnimatePresence>
        </div>
    )
}
